.Home-container {
  -webkit-animation-name: fadein;
  -webkit-animation-duration: 0.5s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-fill-mode: backwards;
  animation-name: fadein;
  animation-duration: 0.5s;
  animation-fill-mode: backwards;
  animation-timing-function: linear;
}

.Home-section {
  font-size: 18px;
  margin: 20px auto;
  border: 2px solid #000000;
  width: 60%;
  min-width: 100px;
  padding: 10px 40px;
}

.Home-list {
  margin: 5px 0 8px 0;
}

.email {
  font-family: "Roboto Condensed";
  font-size: 20px;
  background: rgba(0, 0, 0, 0);
  border: none;
}

.Home-text {
  line-height: 150%;
}

.Home-hold {
  margin: 8px 0;
}

.Home-tooltip {
  position: relative;
  display: inline-block;
}

.Home-tooltip--text {
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  position: absolute;
  z-index: 1;
  bottom: 150%;
  left: 50%;
  margin-left: -60px;
}

.Home-tooltip--text::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: black transparent transparent transparent;
}

.Home-hvrpop {
  font-size: 18px;
  text-decoration: underline;
  cursor: pointer;
  display: inline-block;
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px transparent;
}

.Home-hvrpop:hover,
.Home-hvrpop:focus,
.Home-hvrpop:active {
  color: #f17e33;
  animation-name: hvrpop;
  animation-duration: 0.3s;
  animation-timing-function: linear;
  animation-iteration-count: 1;
}

@keyframes fadein {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes hvrpop {
  50% {
    transform: scale(1.2);
  }
}

@media all and (max-width: 800px) {
  .section {
